import React, { FC } from 'react';
import cn from 'classnames';
import styles from './footer.module.scss';
import { useAppDispatch } from '../../store/hooks';
import { actions } from '../../store/game/slice';
import Winners from '../../game/components/winners';
import Button from '../../common/components/button';

const Footer: FC<any> = () => {
  const dispatch = useAppDispatch();
  
  const handleClick = () => {
    dispatch(actions.setIsWinnersVisible(true));
  }

  return (
    <div className={cn(styles.winners)}>
      <Button outlined={true} onClick={() => handleClick()}>Our lucky Winners</Button>
      <Winners />
    </div>
  );
};

export default Footer;
