import React, { FC } from 'react';
import cn from 'classnames';

import logo from '../../common/assets/logo.svg';
import styles from './header.module.scss';
import { useAppDispatch, useAppSelector } from '../../store/hooks';
import { actions } from '../../store/game/slice';
import { useHistory, useLocation } from 'react-router-dom';
import { GAME_ROUTE, START_ROUTE } from '../../common/components/constants';
import { getIsPlayed, getIsSelected } from '../../store/game/selectors';
import Button from '../../common/components/button';

const Header: FC<any> = () => {
  const dispatch = useAppDispatch();
  const location = useLocation();
  const isPlayed = useAppSelector(getIsPlayed);
  const isSelected = useAppSelector(getIsSelected);
  const history = useHistory();

  const handleStart = () => {
    dispatch(actions.setIsPlayed(true));
  };

  const handleRestart = () => {
    dispatch(actions.reset());
    history.push('/en/start');
  };

  return (
    <div 
      className={cn(styles.headerWrapper, isPlayed && styles.played, isSelected && styles.selected)}
    >
      <img className={cn(styles.logo)} src={logo} alt="logo" />

      {(location.pathname === START_ROUTE) &&
        (<h3>
          <span onClick={() => history.push('/en/start')}>EN</span> | 
          <span onClick={() => history.push('/pt/start')}>PT</span>
          </h3>)
      }

      {(location.pathname === GAME_ROUTE) && (
        <div className={styles.btn}>
          <Button
            onClick={isPlayed ? handleRestart : handleStart}>
              {isPlayed ? 'Back to home' : 'START'}
          </Button>
        </div>
      )}
    </div>
  );
};

export default Header;
