import React, { FC, useEffect, useState } from 'react';
import styles from './game.module.scss';
import cn from 'classnames';
import { getIsPlayed, getIsSelected, getName } from '../../../store/game/selectors';
import { useAppDispatch, useAppSelector } from '../../../store/hooks';
import PizzaSvg from '../../../common/components/pizza';
import { actions } from '../../../store/game/slice';
import { Pizza, WinGame } from '../../../common/components/types';
import Result from '../result';

const Pizzas: Pizza[] = [
  { id: 1, name: 'Your name' },
  { id: 2, name: 'Santiago' },
  { id: 3, name: 'Maria' },
  { id: 4, name: 'Pedro' },
  { id: 5, name: 'Francisco' },
  { id: 6, name: 'Mariana' },
  { id: 7, name: 'Margarida' },
  { id: 8, name: 'Matilde' },
  { id: 9, name: 'JoÃ£o' }
];

const Game: FC = () => {
  const [pizzas, setPizzas] = useState(Pizzas);
  const dispatch = useAppDispatch();
  const isPlayed = useAppSelector(getIsPlayed);
  const isSelected = useAppSelector(getIsSelected);
  const name = useAppSelector(getName);

  useEffect(() => {
    const existingName = pizzas.find((pizza) => pizza.name.toLowerCase() === name.toLowerCase());
    if (existingName) {
      setPizzas(
        pizzas.map((pizza) => {
          if (pizza.name.toLowerCase() === existingName.name.toLowerCase()) {
            return { ...pizza, name: 'Antonio' };
          }
          return pizza;
        })
      );
    }
  }, [name, pizzas]);
  
  const handleSelect = () => {
    if(isPlayed) {
      const randomNumber = Math.floor(Math.random() * 100);

      if(randomNumber <= 11) {
        addWinner(name);
        dispatch(actions.setIsWinner(true));
      } else {
        const randomName: string = pizzas[Math.floor(Math.random() * pizzas.length)].name;
        (randomName === name) ?
          dispatch(actions.setName(pizzas[8].name)) : 
          dispatch(actions.setName(randomName));
      }
      dispatch(actions.setIsSelected(true));
    }
  };

  const addWinner = (name: string) => {
    const existingWinners: string | null = localStorage.getItem('winners');
    const newWinGame: WinGame = {date: new Date(), name}
    let winGames: WinGame[];
    
    if(existingWinners) {
      const data: WinGame[] = JSON.parse(existingWinners);
      winGames = [...data, newWinGame];
    } else {
      winGames = [newWinGame];
      
    }
    localStorage.setItem('winners', JSON.stringify(winGames));
    dispatch(actions.setWinners(winGames));
  }

  return (
    <div className={styles.wrapper}>
      {isSelected ? (
        <Result />
      ):(
        <div className={cn(styles.container, isPlayed && styles.played)}>
          {pizzas.map((pizza, i) => (
            <div
              key={pizza.id} 
              className={cn(styles.pizza)}
              onClick={() => handleSelect()}  
            >
              <div 
                className={cn(styles.imageHolder)}
              >
                <PizzaSvg />
                {!isPlayed && (
                  <h3 className={cn(styles.name)}>{ i === 0 ? name : pizza.name }</h3>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Game;
