import React, { FC } from 'react';
import styles from './winners.module.scss';
import cn from 'classnames';
import { useAppDispatch, useAppSelector } from '../../../store/hooks';
import { getIsWinnersVisible, getWinners } from '../../../store/game/selectors';
import { actions } from '../../../store/game/slice';
import close from '../../../common/assets/close.svg';

const Winners: FC<any> = () => {
  const dispatch = useAppDispatch();
  const isWinnersVisible = useAppSelector(getIsWinnersVisible);
  const winners = useAppSelector(getWinners);

  const handleClick = () => {
    dispatch(actions.setIsWinnersVisible(false));
  };

  return (
    <div 
      className={cn(styles.winners, isWinnersVisible && styles.visible)} 
      onClick={() => handleClick()}
    >
      <div className={cn(styles.contentHolder)}>
        <button className={cn(styles.button)}>our lucky winners</button>
        <img className={cn(styles.close)} src={close} alt="Close button" />
        <ul>
          {winners.length ? (
            winners.map((winner, i) => {
              return (
                <li key={i}>
                  <p>{ winner.name }</p>
                  <span>{ new Date(winner.date).toLocaleDateString() }</span>
                </li>
              )
            })
          ):(
            <p className={styles.empty}>So far, no one has the status of a winner, but you can always become one of them!</p>
          )}
        </ul>
      </div>
    </div>
  );
};

export default Winners;
