import React, { FC, useEffect, useState } from 'react';
import PizzaSvg from '../../../common/components/pizza';
import styles from './result.module.scss';
import cn from 'classnames';
import { useAppSelector } from '../../../store/hooks';
import { getIsWinner, getName } from '../../../store/game/selectors';

const Result: FC<any> = () => {
  const name = useAppSelector(getName);
  const isWinner = useAppSelector(getIsWinner);
  const [isMessageVisible, setIsMessageVisible] = useState<boolean>(false);

  useEffect(() => {
    const timeoutId = setTimeout(() => {
      setIsMessageVisible(true);
    }, 1000);
    return () => clearTimeout(timeoutId);
  }, []);
  
  return (
    <div className={cn(styles.message, isMessageVisible && styles.messageVisible)}>
        <div className={cn(styles.content)}>
        {isWinner ? (
            <>
              <h3 className={cn(styles.textGreen)}>You win!</h3>
              <h4>{ name }</h4>
              <h3 className={cn(styles.textGreen)}>Get your pizza for free</h3>
            </>
          ) : (
            <>
              <h4>{ name }</h4>
              <h3 className={cn(styles.textRed)}>Oops!<br /> Try next time</h3>
            </>
          )}
        </div>
        <div className={cn(styles.imageHolder)}>
          <PizzaSvg large={true} />
        </div>
    </div>
  );
};

export default Result;
